import * as GW from 'gw-utils';
const Flags = GW.map.flags.Cell;
export * from '../dig/site';
export class MapSite extends GW.map.Map {
    constructor(width, height) {
        super(width, height);
        this.machineCount = 0;
        this.machineId = new GW.grid.NumGrid(width, height);
    }
    free() { }
    isSet(x, y) {
        return this.hasXY(x, y) && !this.cell(x, y).isEmpty();
    }
    isDiggable(x, y) {
        if (!this.hasXY(x, y))
            return false;
        const cell = this.cell(x, y);
        if (cell.isEmpty())
            return true;
        if (cell.isWall())
            return true;
        return false;
    }
    isNothing(x, y) {
        return this.hasXY(x, y) && this.cell(x, y).isEmpty();
    }
    isFloor(x, y) {
        return this.isPassable(x, y);
    }
    isBridge(x, y) {
        return this.hasTileFlag(x, y, GW.tile.flags.Tile.T_BRIDGE);
    }
    isDoor(x, y) {
        return this.hasTileFlag(x, y, GW.tile.flags.Tile.T_IS_DOOR);
    }
    isSecretDoor(x, y) {
        return this.hasObjectFlag(x, y, GW.gameObject.flags.GameObject.L_SECRETLY_PASSABLE);
    }
    blocksDiagonal(x, y) {
        return this.hasObjectFlag(x, y, GW.gameObject.flags.GameObject.L_BLOCKS_DIAGONAL);
    }
    blocksPathing(x, y) {
        return (this.hasObjectFlag(x, y, GW.gameObject.flags.GameObject.L_BLOCKS_MOVE) || this.hasTileFlag(x, y, GW.tile.flags.Tile.T_PATHING_BLOCKER));
    }
    blocksItems(x, y) {
        return this.hasObjectFlag(x, y, GW.gameObject.flags.GameObject.L_BLOCKS_ITEMS);
    }
    blocksEffects(x, y) {
        return this.hasObjectFlag(x, y, GW.gameObject.flags.GameObject.L_BLOCKS_EFFECTS);
    }
    isDeep(x, y) {
        return this.hasTileFlag(x, y, GW.tile.flags.Tile.T_DEEP_WATER);
    }
    isShallow(x, y) {
        if (!this.hasXY(x, y))
            return false;
        const cell = this.cell(x, y);
        return (cell.depthTile(GW.gameObject.flags.Depth.LIQUID) &&
            !cell.hasTileFlag(GW.tile.flags.Tile.T_IS_DEEP_LIQUID));
    }
    isAnyLiquid(x, y) {
        if (!this.hasXY(x, y))
            return false;
        const cell = this.cell(x, y);
        return (cell.hasDepthTile(GW.gameObject.flags.Depth.LIQUID) ||
            cell.hasTileFlag(GW.tile.flags.Tile.T_IS_DEEP_LIQUID));
    }
    getTileIndex(x, y) {
        if (!this.hasXY(x, y))
            return 0;
        const cell = this.cell(x, y);
        const tile = cell.highestPriorityTile();
        return tile.index;
    }
    tileBlocksMove(tile) {
        return GW.tile.get(tile).blocksMove();
    }
    backup() {
        const backup = new MapSite(this.width, this.height);
        backup.copy(this);
        backup.machineId.copy(this.machineId);
        backup.machineCount = this.machineCount;
        return backup;
    }
    restore(backup) {
        this.copy(backup);
        this.machineId.copy(backup.machineId);
        this.machineCount = backup.machineCount;
    }
    getChokeCount(x, y) {
        return this.cell(x, y).chokeCount;
    }
    setChokeCount(x, y, count) {
        this.cell(x, y).chokeCount = count;
    }
    isOccupied(x, y) {
        return this.hasItem(x, y) || this.hasActor(x, y);
    }
    analyze() {
        GW.map.analyze(this);
    }
    nextMachineId() {
        return ++this.machineCount;
    }
    getMachine(x, y) {
        return this.machineId[x][y];
    }
    setMachine(x, y, id, isRoom = true) {
        this.machineId[x][y] = id;
        if (id == 0) {
            this.clearCellFlag(x, y, Flags.IS_IN_MACHINE);
        }
        else {
            this.setCellFlag(x, y, isRoom ? Flags.IS_IN_ROOM_MACHINE : Flags.IS_IN_AREA_MACHINE);
        }
    }
}
